<script>
  import { translate } from '../../func/anime';
  import SteamLevel from'steam-level'

  export default {
    name: 'Navbar',
    data() {
      return {
        ssty: {
          backgroundColor: '#a2a2b7'
        },
        showModal: false,
        rate: {
          tf2: 9,
          csgo: 18,
          gems: 700
        },
        rateCalc: {
          tf2: {
            given: 1,
            for: 9
          },
          csgo: {
            given: 1,
            for: 18
          },
          gems: {
            given: 700,
            for: 1
          }
        },
        input: {
          level: 0,
          desired: 0
        }
      }
    },
    methods: {
      delay(ms) {
        return new Promise(res => setTimeout(res, ms));
      },
      async randomColor() {
        await this.delay(2000)
        this.ssty.backgroundColor = '#' + (Math.random() * 0xFFFFFF << 0).toString(16)
        // repeat every 2s and change set a random color
        this.randomColor()
      },
      updateRate() {

          if (this.input.desired > 5000) {
            this.input.desired = 5000
          }

          if (this.input.desired < 0) {
            this.input.desired = 0
          }

          if (this.input.level > 5000) {
            this.input.level = 5000
          }

          if (this.input.level < 0) {
            this.input.level = 0
          }

          let xp = SteamLevel.getTotalXpFromLevel(this.input.desired) -SteamLevel.getTotalXpFromLevel(this.input.level)
          // console.log(this.input.desired, this.input.level, xp)
          this.rateCalc.tf2.given = parseFloat(((xp / this.rate.tf2) / 100).toFixed(1))
          this.rateCalc.tf2.for = parseFloat((this.rateCalc.tf2.given * this.rate.tf2).toFixed(1))

          this.rateCalc.csgo.given = parseFloat(((xp / this.rate.csgo) / 100).toFixed(1))
          this.rateCalc.csgo.for = parseFloat((this.rateCalc.csgo.given * this.rate.csgo).toFixed(1))

          this.rateCalc.gems.given = (xp / 100)  * this.rate.gems
          this.rateCalc.gems.for = (xp / 100)

      },
      to(){
        console.log('to me')
      }
    },
    // computed: {
    //   handleLevel(){

    //     if (this.input.desired >= 1) {

    //     }

    //     console.log()
    //     return console.log();
    //     // let cal = this.input.level
    //   }
    // },
    created() {
      this.input.desired = Math.floor(Math.random() * (100 - 10 + 1) + 10)
      this.updateRate()
      this.randomColor()
    },
    mounted(){
      translate(this.$refs.els1, this.$refs.els2, this.$refs.els3, this.$refs.els4);
      translate(this.$refs.el1, this.$refs.el2, this.$refs.el3, this.$refs.el4);
    }
  }
</script>

